.carrousel {
  position: relative;

  &_img {
    height: 415px;
    width: 100%;
    object-fit: cover;
    border-radius: 25px;
    position: relative;
  }

  &_arrow_right {
    display: flex;
    position: absolute;
    top: 43%;
    right: 2%;
    height: 15%;
    z-index: 10;
    cursor: pointer;
  }

  &_arrow_left {
    display: flex;
    position: absolute;
    top: 43%;
    left: 2%;
    height: 15%;
    z-index: 10;
    cursor: pointer;

    .arrow {
      position: absolute;
      left: 50px;
    }
  }
  &_position {
    position: absolute;
    top: 87%;
    right: 48%;
    z-index: 10;
    color: white;
    font-size: 18px;
  }
}
.housing_info_container {
  display: flex;
  flex-direction: column;
  color: #ff6060;
  .tags {
    display: flex;
    gap: 10px;
    .tag {
      display: flex;
      background-color: #ff6060;
      width: 115px;
      font-size: 14px;
      border-radius: 10px;
      color: white;
      padding: 5px;
      align-items: center;
      justify-content: center;
    }
  }
  .rate_host_container {
    display: flex;
    flex-direction: row-reverse;
    justify-content: space-between;
    align-items: center;
    .host {
      margin-top: 21px;
      display: flex;
      justify-content: flex-end;
      gap: 12px;
      &_name {
        font-size: 18px;
        font-weight: 500;
        width: 73px;
        text-align: end;
      }
      &_picture {
        border-radius: 50%;
        width: 64px;
        height: 64px;
      }
    }
    .stars {
      display: flex;
      gap: 5px;
      .star {
        height: 35px;
      }
    }
  }
}

.housing_collapse {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;

  .collapse {
    width: 100%;
    &_title {
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding-left: 10px;
      padding-right: 10px;
      background-color: #ff6060;
      height: 44px;
      border-radius: 5px;
      margin-top: 20px;
      p {
        font-size: 24px;
        color: white;
        margin: 0px;
        font-weight: 500;
      }
    }
    &_content {
      margin: 0 auto;
      padding-left: 10px;
      padding-right: 10px;
      background-color: #e7e7e760;
      color: #ff6060;
      border-radius: 3px;
      max-height: 0;
      overflow: hidden;
      transition: max-height 0.3s ease;
    }
    &_content.collapsed {
      max-height: 1000px;
      transition: max-height 500ms ease-in-out;
    }
    p {
      width: 100%;
    }
    ul {
      padding-inline-start: 10px;
      li {
        list-style-type: none;
      }
    }
  }
}

@media (min-width: 930px) {
  .main {
    width: 100%;
    .housing_info_container {
      flex-direction: row;
      justify-content: space-between;
      .rate_host_container {
        flex-direction: column;
      }
    }
    .housing_collapse {
      display: flex;
      flex-direction: row;
      gap: 15px;
      align-items: flex-start;
    }
  }
}
